import React, { useState }  from 'react';

const Mission = (props) => {
    const children = props.children;

    const [showDetails, setShowDetails] = useState(false);


    const handleShowDetail = () => {
          if(showDetails){
            setShowDetails(false)
          }
          else{
            setShowDetails(true)
          }
    }
   


    return (
        
        <div className={props.className}>
            <h4>{props.date} :  {props.title}</h4>
            {React.Children.map(children, (child, i) => {
          // Ignore the first child
          if (i == 0) return child
          return 
           })}
           <a onClick={handleShowDetail} ><h5>... Voir plus de détails</h5></a>


        {showDetails && <div className="showInfos">
            <div className="infosContent">
                <div className="head">
                    <h2>{props.title}</h2>
                    <div className="sourceCode">
    
                    </div>
                </div>
                <p className="text"></p>
                {React.Children.map(children, (child, i) => {
          // Ignore the first child
          if (i >= 1) return child
          return 
           })}
                <div onClick={handleShowDetail}  className="button return ">Retourner sur la page </div>
            </div>
        </div>
}



        </div>

        
       

    );
};

export default Mission;